.wishes-section {
	position: relative;

	.wishes-form-wrapper {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-light);
		padding: 5rem 0 0;

		@include max-md {
			padding: 3rem 0 0;
		}

		.bg-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			.image-element {
				height: 100%;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					opacity: 0.2;
				}
			}
		}

		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 30%;
			transform: translate(-50%, 40%) scaleX(-1);

			@include min-md {
				width: 25%;
			}

			@include min-lg {
				width: 15%;
			}
		}

		.orn-2 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 30%;
			transform: translate(40%, 20%);

			@include min-md {
				width: 20%;
			}

			@include min-lg {
				width: 10%;
			}
		}

		.orn-3 {
			position: absolute;
			top: 3rem;
			right: 0;
			width: 30%;
			transform: translate(50%, 0);

			@include min-md {
				width: 20%;
			}

			@include min-lg {
				width: 10%;
			}
		}

		.orn-4 {
			position: absolute;
			top: 80%;
			left: 15%;
			width: 100%;
		}
	}

	.card-form {
		margin-bottom: 0;
		background: transparent;
		position: relative;
		z-index: 3;

		&.card {
			border: none;
		}

		.card-body {
			padding: 0 2rem 2rem;

			@include max-md {
				padding: 0 1rem 2rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 0 0 5rem;

		@include max-sm {
			padding: 0 0 3rem;
		}

		.orn-1 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 30%;
			transform: translate(-50%, -50%);

			@include min-md {
				width: 20%;
			}

			@include min-lg {
				width: 10%;
			}

			.orn-2 {
				position: absolute;
				top: 80%;
				right: 0;
				width: 100%;
			}
		}

		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 30%;
			transform: translate(50%, 0%);

			@include min-md {
				width: 20%;
			}

			@include min-lg {
				width: 10%;
			}
		}

		.card {
			position: relative;
			border: none;
			background-color: transparent;

			.card-body {
				overflow: hidden;
				padding: 2rem 0;

				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);

					&::-webkit-scrollbar {
						width: 5px;
					}

					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}

					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}

					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;

						h6 {
							font-weight: bold;
						}

						h6,
						p {
							margin-bottom: 0;
						}

						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}

						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.8);
							border-radius: 5px;
							position: relative;

							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.8);
							}
						}
					}
				}
			}
		}
	}
}